<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基础信息" name="first">
                <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" @onBasePeople="handleQueryBasePeople" @onPosition="handlePosition"></techo-people>
            </el-tab-pane>
            <el-tab-pane label="位置信息" name="second">
                <vPeoplePosition ref="vPeoplePosition" :xingzhengqu="dic['行政区划']" :disCodeOption="disCodeOption" :isAdd="isAdd"></vPeoplePosition>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="third">
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
                    <el-row :gutter="15">
                      <el-col :span="6">
                          <el-form-item label="矫正人员编号" prop="redressNum">
                            <el-input v-model="dialogForm.redressNum" :maxlength="16" placeholder="请填写矫正人员编号"></el-input>
                          </el-form-item>
                          <el-form-item label="原羁押场所">
                            <el-input v-model="dialogForm.originalPrison" :maxlength="25" placeholder="请填写原羁押场所"></el-input>
                          </el-form-item> 
                          <el-form-item label="矫正开始日期" prop="redressStartDate">
                            <el-date-picker v-model="dialogForm.redressStartDate" type="date" placeholder="请选择日期"></el-date-picker>
                          </el-form-item>
                      </el-col>
                      <el-col :span="6">
                          <el-form-item label="具体罪名" prop="specificCharges">
                            <el-input v-model="dialogForm.specificCharges" :maxlength="25" placeholder="请填写具体罪名"></el-input>
                          </el-form-item>
                          <el-form-item label="原判刑期" prop="originalSentence">
                            <el-input v-model="dialogForm.originalSentence" :maxlength="12" placeholder="请填写原判刑期"></el-input>
                          </el-form-item> 
                          <el-form-item label="矫正结束日期" prop="redressEndDate">
                            <el-date-picker v-model="dialogForm.redressEndDate" type="date" placeholder="请选择日期"></el-date-picker>
                          </el-form-item>
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="矫正类别" prop="redressType" label-width="140px">
                            <el-select placeholder="请选择矫正类别" clearable v-model="dialogForm.redressType">
                                <el-option v-for="(item, index) in dic['矫正类别']" :label="item.name" :value="item.code" :key="index"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="原判刑期开始日期" prop="originalStartDate" label-width="140px">
                            <el-date-picker v-model="dialogForm.originalStartDate" type="date" placeholder="请选择日期"></el-date-picker>
                        </el-form-item>  
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="案件类别" prop="caseType" label-width="140px">
                            <el-cascader placeholder="请选择案件类别" :options="dic['案件类别']" 
                                filterable clearable v-model="dialogForm.caseType" :props="{value: 'code'}">
                            </el-cascader>
                        </el-form-item>
                        <el-form-item label="原判刑期结束日期" prop="originalEndDate" label-width="140px">
                            <el-date-picker v-model="dialogForm.originalEndDate" type="date" placeholder="请选择日期"></el-date-picker>
                        </el-form-item>  
                      </el-col>
                    </el-row>
                    <div class="cut-apart-line"></div>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="接收方式" prop="receiveMethod">
                                <el-select placeholder="请选择接收方式" clearable v-model="dialogForm.receiveMethod">
                                    <el-option v-for="(item, index) in dic['接收方式']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="是否惯犯" prop="isrecidivist">
                                <el-radio v-model="dialogForm.isrecidivist" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.isrecidivist" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="四史情况">
                                <el-select placeholder="请选择'四史'情况" clearable multiple v-model="dialogForm.fourSituation">
                                    <el-option v-for="(item, index) in dic['四史情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="是否建立矫正小组" prop="isgroup" label-width="140px"> 
                                <el-radio v-model="dialogForm.isgroup" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.isgroup" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="三涉情况" label-width="140px">
                                <el-select placeholder="请选择'三涉'情况" clearable multiple v-model="dialogForm.threeSituation">
                                    <el-option v-for="(item, index) in dic['三涉情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="矫正小组人员组成" label-width="140px">
                                <el-select placeholder="矫正小组人员组成类别" clearable multiple :disabled="dialogForm.isgroup == 0" v-model="dialogForm.groupComponent">
                                    <el-option v-for="(item, index) in dic['矫正小组人员类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="矫正解除类型" prop="redressLiftedType" label-width="140px">
                                <el-select placeholder="请选择矫正解除类型" clearable v-model="dialogForm.redressLiftedType">
                                    <el-option v-for="(item, index) in dic['矫正解除类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <div class="cut-apart-line"></div>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="奖惩情况">
                                <el-input v-model="dialogForm.rewardsSituation" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写奖惩情况"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="刑罚变更执行">
                                <el-input v-model="dialogForm.penaltyChanges" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写刑罚变更执行情况"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="是否重新犯罪" label-width="140px" prop="iscrimeagin">
                                <el-radio v-model="dialogForm.iscrimeagin" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.iscrimeagin" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="重新犯罪名称" v-if="dialogForm.iscrimeagin == 1" label-width="140px" prop="crimeagainName">
                                <el-input v-model="dialogForm.crimeagainName" :maxlength="25" placeholder="请填写重新犯罪名称"></el-input>
                            </el-form-item>  
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="是否有脱管" prop="ishosted">
                                <el-radio v-model="dialogForm.ishosted" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.ishosted" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="脱管原因" v-if="dialogForm.ishosted == 1" prop="hostedReasons">
                                <el-input v-model="dialogForm.hostedReasons" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写脱管原因"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="检查监督脱管情况" v-if="dialogForm.ishosted == 1" label-width="140px" prop="hostedSituation">
                                <el-input v-model="dialogForm.hostedSituation" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写检查监督脱管情况"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="脱管纠正情况" v-if="dialogForm.ishosted == 1" label-width="140px" prop="hostedCorrectSituation">
                                <el-input v-model="dialogForm.hostedCorrectSituation" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写脱管纠正情况"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="是否有漏管" prop="isdraintube">
                                <el-radio v-model="dialogForm.isdraintube" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.isdraintube" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="漏管原因" v-if="dialogForm.isdraintube == 1" prop="draintubeReasons">
                                <el-input v-model="dialogForm.draintubeReasons" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写漏管原因"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="检查监督漏管情况" v-if="dialogForm.isdraintube == 1" label-width="140px" prop="draintubeSituation">
                                <el-input v-model="dialogForm.draintubeSituation" :disabled="dialogForm.isdraintube == 0" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写检查监督漏管情况"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="漏管纠正情况" v-if="dialogForm.isdraintube == 1" label-width="140px" prop="draintubeCorrectSituation">
                                <el-input v-model="dialogForm.draintubeCorrectSituation" :disabled="dialogForm.isdraintube == 0" type="textarea" :rows="2" :maxlength="1024" placeholder="请填写漏管纠正情况"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/special_people_service.js";
import vPeoplePosition from "../infact_peoples/components/PeoplePosition.vue";

export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  components: {
    vPeoplePosition
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        矫正类别: [],
        案件类别: [],
        接收方式: [],
        四史情况: [],
        三涉情况: [],
        矫正小组人员类型: [],
        矫正解除类型: [],
        行政区划: []
      },
      dialogForm: {
        isrecidivist: 0,
        isgroup: 1,
        ishosted: 0,
        isdraintube: 0,
        iscrimeagin: 0,
        groupComponent: [],
        fourSituation: [],
        threeSituation: []
      },
      teamRules: {
        redressNum: [
          {
            required: true,
            message: "请输入社区矫正人员编号",
            trigger: "blur"
          },
          { min: 2, max: 16, message: "长度在 2 到 16 个字符", trigger: "blur" }
        ],
        redressType: [
          { required: true, message: "请选择矫正类别", trigger: "change" }
        ],
        caseType: [
          { required: true, message: "请选择案件类别", trigger: "change" }
        ],
        specificCharges: [
          { required: true, message: "请输入具体罪名", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        redressStartDate: [
          { required: true, message: "请选择矫正开始日期", trigger: "change" }
        ],
        redressEndDate: [
          { required: true, message: "请选择矫正结束日期", trigger: "change" }
        ],
        receiveMethod: [
          { required: true, message: "请选择接收方式", trigger: "change" }
        ],
        crimeagainName: [{ required: true, message: "请填写重新犯罪名称", trigger: "blur" }],
        hostedReasons: [{ required: true, message: "请填写托管原因", trigger: "blur" }],
        hostedSituation: [{ required: true, message: "请填写检查监督脱管情况", trigger: "blur" }],
        hostedCorrectSituation: [{ required: true, message: "请填写脱管纠正情况", trigger: "blur" }],
        draintubeReasons: [{ required: true, message: "请填写漏管原因", trigger: "blur" }],
        draintubeSituation: [{ required: true, message: "请填写检查监督漏管情况", trigger: "blur" }],
        draintubeCorrectSituation: [{ required: true, message: "请填写漏管纠正情况", trigger: "blur" }],
      },
      houseInfo: {}
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else if (item.name == "案件类别") {
              that.dic[item.name] = that.handleCaseTypeTree(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].baseForm.id &&
          service.requestBaseIdByPeople(
            "people/comcor/findPage",
            this.$refs["vBaseForm"].baseForm.id,
            data => {
              if (data.totalCount != 0) {
                this.isAdd = false;
                this.dialogForm = data.list[0];
              }
            }
          );
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
        this.$refs["vPeoplePosition"].$refs["houseItem"] &&
          this.$refs["vPeoplePosition"].$refs["houseItem"].initGridData();
        this.$refs["vPeoplePosition"].$refs["liveHouseItem"] &&
          this.$refs["vPeoplePosition"].$refs["liveHouseItem"].initGridData();
      } else if (name == "third") {
        new Promise((resolve, reject) => {
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            this.$refs["vPeoplePosition"].$refs["houseItem"].$refs[
              "houseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
            this.houseInfo = this.$refs["vPeoplePosition"].$refs[
              "houseItem"
            ].houseInfo;
            resolve();  
          } else {
            service.requestHouseInfo(
              this.$refs["vBaseForm"].baseForm.houseId,
              data => {
                this.houseInfo = data;
                resolve();  
              }
            );
          };
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            this.$refs["vPeoplePosition"].$refs["liveHouseItem"].$refs[
              "liveHouseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
          };
        }).then(() => {
          this.dialogForm = {
            ...this.dialogForm,
            districtCode: this.houseInfo.districtCode
          };
        });
      }
    },
    // 是否建立矫正小组
    "dialogForm.isgroup"(value) {
      value == 0 && (this.dialogForm.groupComponent = []);
    },
    // 是否脱管
    "dialogForm.ishosted"(value) {
        if(value == 0) {
            this.dialogForm.hostedReasons = "";
            this.dialogForm.hostedSituation = "";
            this.dialogForm.hostedCorrectSituation = "";
        }
    },
    // 是否漏管
    "dialogForm.isdraintube"(value) {
        if(value == 0) {
            this.dialogForm.draintubeReasons = "";
            this.dialogForm.draintubeSituation = "";
            this.dialogForm.draintubeCorrectSituation = "";
        }
    },
    // 是否重新犯罪
    "dialogForm.iscrimeagin"(value) {
        value == 0 && (this.dialogForm.crimeagainName = "");
    },
  },

  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    // 案件类别
    handleCaseTypeTree(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(2, 6) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 2) === item.code.slice(0, 2) &&
              street.code.slice(2, 6) !== "0000" &&
              street.code.slice(4, 6) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 4) === item.code.slice(0, 4) &&
                street.code.slice(4, 6) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 身份证号选择后回填位置信息 */
    handlePosition(item) {
        this.$refs["vPeoplePosition"].hujidi = item.domicilePlaceDetail;
        this.$refs["vPeoplePosition"].xianzhudi = item.livePlaceDetail;
        this.$refs["vPeoplePosition"].showHujiForm = false;
        this.$refs["vPeoplePosition"].showXianzhuForm = false;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate = baseInfo.birthDate && util.time.formatDate(
            baseInfo.birthDate,
            "YYYY-MM-DD"
          );
          baseInfo.occupationType = _.last(baseInfo.occupationType);
          baseInfo.districtCode = this.houseInfo.districtCode;
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            let houseAddress = this.$refs["vPeoplePosition"].$refs["houseItem"]
              .positionForm;
            baseInfo.domicilePlace = houseAddress.province; // 户籍地
            baseInfo.domicilePlaceDetail = houseAddress.domicilePlaceDetail; // 户籍地详址
            baseInfo.houseId = houseAddress.houseId; // 户籍地户ID
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            let liveAddress = this.$refs["vPeoplePosition"].$refs[
              "liveHouseItem"
            ].positionForm;
            baseInfo.livePlace = liveAddress.province; // 居住地
            baseInfo.livePlaceDetail = liveAddress.domicilePlaceDetail; // 居住地详址
            baseInfo.liveHouseId = liveAddress.houseId || ""; // 居住地户ID
          }
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      otherInfo.originalStartDate = otherInfo.originalStartDate && util.time.formatDate(
        otherInfo.originalStartDate,
        "YYYY-MM-DD"
      );
      otherInfo.originalEndDate = otherInfo.originalEndDate && util.time.formatDate(
        otherInfo.originalEndDate,
        "YYYY-MM-DD"
      );
      otherInfo.redressStartDate = otherInfo.redressStartDate && util.time.formatDate(
        otherInfo.redressStartDate,
        "YYYY-MM-DD"
      );
      otherInfo.redressEndDate = otherInfo.redressEndDate && util.time.formatDate(
        otherInfo.redressEndDate,
        "YYYY-MM-DD"
      );
      otherInfo.groupComponent = otherInfo.groupComponent.join(",");
      otherInfo.fourSituation = otherInfo.fourSituation.join(",");
      otherInfo.threeSituation = otherInfo.threeSituation.join(",");

      otherInfo.caseType = _.last(otherInfo.caseType);
      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 社区矫正人员附加信息
        service.requestSaveCommPeople(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "社区矫正人员添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改
        let param = _.pick(otherInfo, [
          "caseType",
          "crimeagainName",
          "districtCode",
          "draintubeCorrectSituation",
          "draintubeReasons",
          "draintubeSituation",
          "fourSituation",
          "groupComponent",
          "hostedCorrectSituation",
          "hostedReasons",
          "hostedSituation",
          "id",
          "iscrimeagin",
          "isdraintube",
          "isgroup",
          "ishosted",
          "isrecidivist",
          "originalEndDate",
          "originalPrison",
          "originalSentence",
          "originalStartDate",
          "penaltyChanges",
          "peopleId",
          "receiveMethod",
          "redressEndDate",
          "redressLiftedType",
          "redressNum",
          "redressStartDate",
          "redressType",
          "rewardsSituation",
          "specificCharges",
          "threeSituation"
        ]);
        // 修改
        service.requestUpdateCommPeople(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "社区矫正人员修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["vBaseForm"].resetBaseForm();
      this.$refs["dialogForm"].resetFields();
      this.$refs["vPeoplePosition"].destroyedForm();
      this.dialogForm = {
        isrecidivist: 0,
        isgroup: 1,
        ishosted: 0,
        isdraintube: 0,
        iscrimeagin: 0,
        groupComponent: [],
        fourSituation: [],
        threeSituation: []
      };
      this.houseInfo = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
    }
  }
};
</script>
