<template>
  <div class="table-list-page">
    <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
      @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
      @onTreeNode="handleTreeNode">
      <div slot="header">
        <el-row type="flex" justify="space-around" class="techo-list__header-row">
          <el-col :span="10">
            <span class="techo-list__header-col-txt-4">姓名：</span>
            <div class="techo-list__header-col-left-4">
              <el-input placeholder="请输入姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                clearable v-model="queryForm.name"></el-input>
            </div>
          </el-col>
          <el-col :span="9">
          </el-col>
          <el-col :span="5" class="techo-list__header-col-button">
            <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
            <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </techo-list>
    <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="title"></techo-details>
    <vAddDialog ref="vAddDialog" :disCodeOption="disCodeOption"></vAddDialog>
  </div>
</template>

<script>
import service from "./services/special_people_service.js";
import vAddDialog from "./community-people-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "社区矫正人员",
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        姓名: "name",
        原羁押场所: "originalPrison",
        // 社区矫正人员编号: "redressNum",
        矫正类别: "dicMap.redressType",
        案件类别: "dicMap.caseType",
        具体罪名: "specificCharges",
        // 原判刑期: "originalSentence",
        // 原判刑期开始日期: "originalStartDate",
        // 原判刑期结束日期: "originalEndDate",
        矫正开始日期: "redressStartDate",
        矫正结束日期: "redressEndDate",
        // 接收方式: "dicMap.receiveMethod",
        // "'四史'情况": "dicMap.fourSituation",
        // 是否惯犯: "dicMap.isrecidivist",
        // "'三涉'情况": "dicMap.threeSituation",
        // 是否建立矫正小组: "dicMap.isgroup",
        // 矫正小组人员组成: "dicMap.groupComponent",
        矫正解除类型: "dicMap.redressLiftedType",
        是否有脱管: "dicMap.ishosted",
        // 脱管原因: "hostedReasons",
        // 检查监督脱管情况: "hostedSituation",
        // 脱管纠正情况: "hostedCorrectSituation",
        是否有漏管: "dicMap.isdraintube",
        // 漏管原因: "draintubeReasons",
        // 检查监督漏管情况: "draintubeSituation",
        // 漏管纠正情况: "draintubeCorrectSituation",
        // 奖惩情况: "rewardsSituation",
        // 刑罚变更执行情况: "penaltyChanges",
        是否重新犯罪: "dicMap.iscrimeagin"
        // 重新犯罪名称: "crimeagainName",
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "communityPeople"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加社区矫正人员 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
    },
    /** 删除社区矫正人员 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteCommPeople(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "社区矫正人员删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "社区矫正人员删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      // 获取基本信息
      service.requestBasePeople(row.peopleId, data => {
        if (data.occupationType != null && data.occupationType != "") {
          let occType = [
            data.occupationType.substr(0, 1) + "0000",
            data.occupationType.substr(0, 3) + "00",
            data.occupationType
          ];
          data.occupationType = _.uniq(occType);
        } else {
          data.occupationType = [];
        }
        this.$refs["vAddDialog"].$refs["vBaseForm"].baseForm = data;
        this.$refs["vAddDialog"].handlePosition(data);
      });
      let data = _.cloneDeep(row);
      data.groupComponent =
        (data.groupComponent && data.groupComponent.split(",")) || [];
      data.fourSituation =
        (data.fourSituation && data.fourSituation.split(",")) || [];
      data.threeSituation =
        (data.threeSituation && data.threeSituation.split(",")) || [];
      if (data.caseType != null && data.caseType != "") {
        let occType = [
          data.caseType.substr(0, 2) + "0000",
          data.caseType.substr(0, 4) + "00",
          data.caseType
        ];
        data.caseType = _.uniq(occType);
      } else {
        data.caseType = [];
      }
      this.$refs["vAddDialog"].dialogForm = data;
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageCommPeople(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageCommPeople(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              if (item.dicMap) {
                item.dicMap.isrecidivist = item.isrecidivist == 1 ? "是" : "否";
                item.dicMap.isgroup = item.isgroup == 1 ? "是" : "否";
                item.dicMap.ishosted = item.ishosted == 1 ? "是" : "否";
                item.dicMap.isdraintube = item.isdraintube == 1 ? "是" : "否";
                item.dicMap.iscrimeagin = item.iscrimeagin == 1 ? "是" : "否";
              }
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            if (item.dicMap) {
              item.dicMap.isrecidivist = item.isrecidivist == 1 ? "是" : "否";
              item.dicMap.isgroup = item.isgroup == 1 ? "是" : "否";
              item.dicMap.ishosted = item.ishosted == 1 ? "是" : "否";
              item.dicMap.isdraintube = item.isdraintube == 1 ? "是" : "否";
              item.dicMap.iscrimeagin = item.iscrimeagin == 1 ? "是" : "否";
            }
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
